import { Component, OnInit, Input } from "@angular/core";
import { log } from "util";

@Component({
    selector: "app-item",
    templateUrl: "./item.component.html",
    styleUrls: ["./item.component.scss"]
})
export class ItemComponent implements OnInit {
    @Input() item: object;
    show;
    constructor() {}

    ngOnInit() {}

    showEvent(){
      this.show = false;
      this.show = true;
    }
  closeEvent(){
      this.show = false;
  }
}



