import { Component, OnInit } from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {FilterService} from "../../../shared/filter.service";

@Component({
    selector: "app-items",
    templateUrl: "./items.component.html",
    styleUrls: ["./items.component.scss"]
})
export class ItemsComponent implements OnInit {
    arr = [
        {
            name: "Mother Earth Hosts Our Travels",
            description: "",
            oldPrice: "10.5",
            discountedPrice: "50.23",
            imagePath: "../../../../../assets/img/camping1.jpg",
            url: "",
            plattform: "",
            category: "camping",
            tag: ""
            // тут 12 разных обьектов
        },
        {
            name: "Take A Romantic Break In A Boutique Hotel",
            description: "",
            oldPrice: "30.8",
            discountedPrice: "44.59",
            imagePath: "../../../../../assets/img/camping2.jpg",
            url: "",
            plattform: "",
            category: "camping",
            tag: ""
        },
        {
            name: "The Luxury Of Traveling With Yacht Charter Companies",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/photo1.jpg",
            url: "",
            plattform: "",
            category: "photo",
            tag: ""
        },
        {
            name:
                "It S Hurricane Season But We Are Visiting Hilton Head Island",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/clothing1.jpg",
            url: "",
            plattform: "",
            category: "clothing",
            tag: ""
        },
        {
            name: "Get Ready Fast For Fall Leaf Viewing Trips",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "60.5",
            imagePath: "../../../../../assets/img/clothing2.jpg",
            url: "",
            plattform: "",
            category: "clothing",
            tag: ""
        },
        {
            name: "Tremblant In Canada",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "58.5",
            imagePath: "../../../../../assets/img/outdoor1.jpg",
            url: "",
            plattform: "",
            category: "outdoor",
            tag: ""
        },
        {
            name: "Big Savings On Gas While You Travel",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/outdoor2.jpg",
            url: "",
            plattform: "",
            category: "outdoor",
            tag: ""
        },
        {
            name: "Les Houches The Hidden Gem Of The Chamonix Valley",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/photo1.jpg",
            url: "",
            plattform: "",
            category: "photo",
            tag: ""
        },
        {
            name: "How To Fly A Private Jet On Your Next Trip",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/photo2.jpg",
            url: "",
            plattform: "",
            category: "photo",
            tag: ""
        },
        {
            name: "How To Fly A Private Jet On Your Next Trip",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/camping1.jpg",
            url: "",
            plattform: "",
            category: "camping",
            tag: ""
        },
        {
            name: "How To Fly A Private Jet On Your Next Trip",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/clothing2.jpg",
            url: "",
            plattform: "",
            category: "clothing",
            tag: ""
        },
        {
            name: "How To Fly A Private Jet On Your Next Trip",
            description: "",
            oldPrice: "5.55",
            discountedPrice: "68.5",
            imagePath: "../../../../../assets/img/outdoor2.jpg",
            url: "",
            plattform: "",
            category: "outdoor",
            tag: ""
        }
    ];

    filterData = [];

    constructor(private route: ActivatedRoute, private router: Router, private filterService: FilterService) {
      this.filterService.changePrice.subscribe((data) => {
        this.priceSort(data);
      })
    }

    ngOnInit() {
      this.filterData = Object.assign([], this.arr);
      this.route.params.subscribe(param =>{
        if(param['sort'] === 'price'){
          this.priceSort(true, this.arr);
        }

        if(param['type'] === 'everything'){
          this.filterData = Object.assign([], this.arr);
          return;
        }

        this.filterData = Object.assign([], this.arr).filter(
          item => {
            return item["category"] === param['type']
          }
        );
      }
    );
    }

    priceSort(b, sortA?){
      let sortArr;
      if(sortA){
        sortArr = sortA;
      } else {
        sortArr = this.filterData;
      }
      if(b){
        sortArr = sortArr.sort((t1, t2) => {

          if (+t1.discountedPrice > +t2.discountedPrice) {
            return 1;
          }

          if (+t1.discountedPrice < +t2.discountedPrice) {
            return -1;
          }

          return 0;
        })
      } else {
        sortArr = sortArr.sort((t1, t2) => {

          if (+t1.discountedPrice < +t2.discountedPrice) {
            return 1;
          }

          if (+t1.discountedPrice > +t2.discountedPrice) {
            return -1;
          }

          return 0;
        })
      }
    }
}
class Item {
    public name: string;
    public description: string;
    public oldPrice: string;
    public discountedPrice: string;
    public imagePath: string;
    public url: string;
    public plattform: string;
    public category: string;
    public tag: string;
}



