import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Routes, Router } from "@angular/router";
import {FilterService} from "../../shared/filter.service";

@Component({
    selector: "app-filter-buttons",
    templateUrl: "./filter-buttons.component.html",
    styleUrls: ["./filter-buttons.component.scss"]
})
export class FilterButtonsComponent implements OnInit {
    type = "";
    arrowSwitch = true;
    constructor(private route: ActivatedRoute, private router: Router, private filterService: FilterService) {
      this.type = this.route.snapshot.params["sort"]
    }

    ngOnInit() {}
    go(str) {
      this.type = str;
      if(str === 'price'){
        this.arrowSwitch = !this.arrowSwitch;
        this.filterService.changePrice.emit(this.arrowSwitch);
      }
        let type = this.route.snapshot.params["type"];
        this.router.navigate(["/" + type + "/" + str]);
    }
}



