import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";

import { AppRoutingModule } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { ListWrapComponent } from "./components/wrapper/list-wrap/list-wrap.component";
import { ItemsComponent } from "./components/wrapper/list-wrap/items/items.component";
import { ItemComponent } from "./components/wrapper/list-wrap/items/item/item.component";
import { AddYourProductComponent } from "./components/wrapper/add-your-product/add-your-product.component";
import { HeaderComponent } from "./components/header/header.component";
import { LeftMenuComponent } from "./components/wrapper/left-menu/left-menu.component";
import { WrapperComponent } from "./components/wrapper/wrapper.component";
import { ContactComponent } from "./components/contact/contact.component";
import { ItemModalComponent } from './components/wrapper/list-wrap/items/item/item-modal/item-modal.component';
import {FilterButtonsComponent} from "./components/wrapper/filter-buttons/filter-buttons.component";
import {FilterService} from "./components/shared/filter.service";

@NgModule({
    declarations: [
        AppComponent,
        ListWrapComponent,
        ItemsComponent,
        ItemComponent,
        FilterButtonsComponent,
        AddYourProductComponent,
        HeaderComponent,
        LeftMenuComponent,
        WrapperComponent,
        ContactComponent,
        ItemModalComponent
    ],
    imports: [BrowserModule, AppRoutingModule],
    providers: [FilterService],
    bootstrap: [AppComponent]
})
export class AppModule {}



